$main-color: #232323;
body{
    background-color: $main-color;
    color: #fff;
    font-family: Fira Code;
    overflow: hidden;
}
.content-todo{
    max-width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.content__icon{
    display: flex;
    flex-direction: row;
    margin: 20px;
}
.content__title{
    font-weight: normal;
    font-size: 64px;
    line-height: 16px;
    align-items: center;
    text-align: center;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    margin-left: 20px;
}
.app-todo{
    width: 1700px;
    height: 650px;
    border-radius: 20px;
    border: 7px #fff solid;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.task{
    width: 220px;
    height: 250px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
}
.task__input{
    width: 100%;
    height: 220px;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: -1.5px;
    text-transform: uppercase;
    color: #ffffff;
    text-align: center;
    background-color: $main-color;
    border: 5px solid #fff;
    border-radius: 16px;
}
.task__checkbox{
    width: 22px;
    height: 22px;
    margin: 10px;
}
.task__color{
    width: 20px;
    height: 100%;
    background-color: red;
}
.buttons{
    display: flex;
    justify-content: space-around;
    background-color: $main-color;
    border: 7px solid #fff;
    border-radius: 20px;
    width: 500px;
    height: 70px;
    padding: 5px;
    transform: translateY(-50px);
}